// SC: AWEBOOKING
.fleurdesel-awebooking {
  .awebooking-wrapper {
    background-color: transparent !important;
  }

  .apb-field {
    position: relative;
    margin-bottom: 20px !important;
    display: block !important;

    @include media(">=md") {
      display: inline-block !important;
      float: left;
      width: 50%;
      margin: 0 0 20px !important;
      padding: 0 10px !important;
    }

    @include media(">=xl") {
      float: none;
      width: auto;
      margin-right: 8.5px !important;
      margin-left: 8.5px !important;
      padding: 0 !important;
    }

    label {
      font-family: $font-1;
      font-size: 1rem !important;
      font-style: italic;
      font-weight: 400;
      text-transform: none;
      color: $color-1;

      @include media(">=xl") {
        padding: 0 3px;
        margin: 0;
        z-index: 2;
        transform: translateY(-50%);

        @include absolute(50%,null,null,30px);
      }

      &:after {
        @include media(">=xl") {
          content: "";
          display: block;
          width: 100%;
          height: 1px;
          background-color: $color-1;
          z-index: 3;

          @include absolute(null,null,0);
        }
      }
    }

    &.apb-field-none {
      display: none !important;
    }
  }

  .apb-field-group {
    display: block !important;
    border: 1px solid #e4e4e4;
    background-color: #fff;

    @include media(">=xl") {
      display: inline-block !important;
    }

    @include media(">=xl") {
      padding-left: 140px;
    }
  }

  .apb-select, .apb-input {
    border: none !important;
    background-color: transparent !important;
    padding: 5px 30px 5px 20px !important;
  }

  .apb-calendar {
    width: 150px !important;

    @include media(">=1300px","<1600px") {
      width: 100% !important;
      padding: 0 !important;
    }
  }

  .apb-input.apb-calendar::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: #fff;
  }

  .apb-input.apb-calendar::-moz-placeholder { /* Firefox 19+ */
    color: #fff;
  }

  .apb-input.apb-calendar:-ms-input-placeholder { /* IE 10+ */
    color: #fff;
  }

  .apb-input.apb-calendar:-moz-placeholder { /* Firefox 18- */
    color: #fff;
  }

  .apb-field--night,
  .apb-field--rooms,
  .apb-field--adult,
  .apb-field--child {
    .apb-field-group {
      @include media(">=xl") {
        padding-left: 80px;
      }
    }
  }

  .list-room {
    display: inline-block;
  }

  .apb-btn {
    display: block !important;
    width: auto;
    margin: auto;
    border: 2px solid $color-1 !important;
    color: #fff;

    &:hover {
      background-color: darken($color-1, 15%) !important;
      border-color: darken($color-1, 15%) !important;
    }
  }
}

// STANDARD 1
.fleurdesel-awebooking--standard-1 {
  .apb-field label {
    color: #fff !important;

    @include media(">=xl") {
      color: $color-1 !important;
    }
  }

  .apb-select, .apb-input {
    font-size: 1.5625rem !important;
    height: 70px !important;
  }

  .apb-calendar {
    width: 100% !important;

    @include media(">=xl") {
      width: 170px !important;
    }
  }

  .apb-btn {
    min-width: 255px;
    background-color: transparent !important;

    &:hover {
      color: #fff !important;
    }
  }
}

// STANDARD 2
.fleurdesel-awebooking--standard-2 {
  display: none;

  @include media(">=xl") {
    display: block;
  }

  .apb-content {
    padding: 0;
  }

  .apb-field {
    float: left;
    margin: 0 !important;
  }

  .apb-field-group {
    border: none;
    border-left: 1px solid #e4e4e4;

    @include media(">=1460px") {
      padding-left: 160px;
    }
  }

  @include media(">=1460px") {
    .apb-field-group i {
      right: 30px;
    }

    .apb-field-group .apbf-select {
      right: 30px;
    }

    .apb-field--adult .apb-field-group {
      padding-left: 100px;
    }

  }

  .apb-field--child .apb-field-group {
    padding-left: 100px;

    @include media(">=1460px") {
      padding-left: 125px;
    }
  }

  .apb-select, .apb-input {
    height: 70px !important;
    font-size: 1.5625rem !important;
    padding-bottom: 5px !important;

    @include media(">=1460px") {
      padding: 0px 60px 5px 0px !important;
    }
  }

  .apb-calendar {
    width: 100% !important;

    @include media(">=xl") {
      width: 170px !important;
    }

    @include media(">=1460px") {
      width: 180px !important;
    }
  }

  .apb-btn {
    height: 70px !important;
    top: 0 !important;
  }
}

// STANDARD 3
.fleurdesel-awebooking--standard-3 {
  .apb-content {
    background-color: #fff;
    padding: 30px 20px;
    box-shadow: 0px 10px 30px 0px rgba(1, 1, 1, 0.1);
  }

  .apb-field {
    display: block !important;
    margin: 0 !important;
    padding-bottom: 20px !important;
    text-align: left;
    vertical-align: middle;

    @include media(">=md") {
      display: inline-block !important;
    }

    @include media(">=xl") {
      padding-bottom: 0 !important;
    }

    &:not(:first-child):not(:last-child) {
      @include media(">=md") {
        border-left: 1px solid #e4e4e4;
      }
    }

    &:nth-child(3n+1) {
      @include media("<xl") {
        border-left: none !important;
      }
    }

    label {
      position: relative;
      top: auto;
      left: auto;
      display: inline-block;
      padding: 5px 20px 5px 20px !important;
      line-height: 1;
      transform: translateY(0);

      @include media(">=md") {
        padding: 5px 50px 5px 50px !important;
      }

      &:after {
        display: none;
      }
    }
  }

  .apb-field-group {
    display: block !important;
    border: none;
    margin-top: -10px;

    @include media(">=md") {
      padding-left: 30px !important;
      padding-right: 30px !important;
    }

    i {
      right: 20px;

      @include media(">=md") {
        right: 50px;
      }
    }
  }

  .apb-field--night,
  .apb-field--rooms,
  .apb-field--adult,
  .apb-field--child {
    .apb-field-group {
      @include media(">=xl") {
        padding-left: 0;
      }
    }
  }

  .apb-select, .apb-input {
    height: 45px !important;
    font-size: 1.5625rem !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
  }

  .apb-calendar {
    width: 100% !important;

    @include media(">=xl") {
      width: 180px !important;
    }
  }

  .apb-field--action {
    display: block !important;
    width: 100%;
    margin-top: 20px !important;
    padding-bottom: 0 !important;

    @include media(">=xl") {
      padding-left: 50px !important;
      display: inline-block !important;
      width: auto !important;
      margin-top: 0 !important;
    }
  }

  .apb-btn {
    height: 70px !important;
  }

  .apb-input::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: #111;
  }

  .apb-input::-moz-placeholder { /* Firefox 19+ */
    color: #111;
  }

  .apb-input:-ms-input-placeholder { /* IE 10+ */
    color: #111;
  }

  .apb-input:-moz-placeholder { /* Firefox 18- */
    color: #111;
  }
}

// STANDARD 4
.fleurdesel-awebooking--standard-4 {
  .apb-btn {
    background-color: $color-1 !important;
    border-color: $color-1 !important;
    color: #fff !important;

    &:hover {
      background-color: darken($color-1, 15%) !important;
      border-color: darken($color-1, 15%) !important;
      color: #fff !important;
    }
  }
}

// MODERN
.fleurdesel-awebooking--modern {
  .apb-content {
    padding: 0;
  }

  .apb-field {
    margin: 0 0 30px !important;
    padding: 50px !important;
    width: 100% !important;
    min-height: 251px;
    display: block !important;
    text-align: center !important;
    border: 1px solid #e4e4e4;
    background-color: #fff;
    transition: all .4s ease;

    &:hover {
      box-shadow: 0px 10px 30px 0px rgba(1, 1, 1, 0.1);
    }

    label {
      position: static;
      text-align: center;
      font-size: 0.75rem !important;
      font-weight: 700;
      font-style: normal;
      font-family: $font-2;
      letter-spacing: 2px;
      color: #000;
      text-transform: uppercase;
      transform: translateY(0);

      &:after {
        display: none;
      }
    }
  }

  .apb-field-group {
    width: 100% !important;
    display: block !important;
    padding-left: 0 !important;
    border: none !important;

    i {
      font-size: 15px !important;
      color: $color-1 !important;
    }

    span {
      display: block;
      font-family: $font-1;
    }

    .select2 {
      display: inline-block;

      * {
        outline: none !important;
      }
    }
  }

  .apb-input,
  .apb-select,
  .apb-calendar,
  .select2-selection__rendered {
    display: inline-block !important;
    width: 100px;
    height: auto !important;
    line-height: 1.5 !important;
    font-size: 3.75rem !important;
    color: $color-1 !important;
    text-align: center;
    outline: none;
  }

  .apb-btn {
    margin-top: 20px !important;
  }

  .select2-hidden-accessible {
    display: none !important;
  }
}

.select2-container {
  z-index: 17;
}

.select2-dropdown {
  .select2-search {
    display: none !important;
  }

  .select2-results__options {
    background-color: #fff;
    width: 100px;
    z-index: 17;
    border: 1px solid #e4e4e4;
  }

  ul {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  .select2-results__option {
    padding: 5px 20px;

    &:not(:last-child) {
      border-bottom: 1px solid #e4e4e4;
    }

    &[aria-selected="true"] {
      background-color: $color-1;
      color: #fff;
    }
  }

  .select2-results__option--highlighted {
    background-color: #338FFF;
    color: #fff;
  }
}

.awebooking-accordion__header {
  font-size: 18px;
}

.awebooking-cart-item__title {
  font-size: 20px;
}

.awebooking-cart-item__reservation-title {
  font-size: 15px;
}

.awebooking-cart-item__price strong {
  display: none;
}

.awebooking-button {
  cursor: pointer;
}

.awebooking-cart__buttons {
  text-align: center;
  margin-top: 30px;
  .awebooking-button {
    padding: 15px 15px;
    color: #fff;
    background-color: #b98036;
    border-color: #b98036;
    line-height: 1.25;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;

    &:hover {
      background-color: #92652a;
      border-color: #8a5f28;
    }
  }
}

.awebooking-select {
  cursor: pointer;
}

.fleurdesel-awebooking--standard-2.has-children.has-infants {
  .apb-select, .apb-input {
    padding-right: 40px !important;
  }

  .apb-field--child,
  .apb-field--infants,
  .apb-field--adult {
    .apb-field-group {
      padding-left: 100px;
    }
  }
}
.fleurdesel-awebooking--standard-3.has-children.has-infants {
  .apb-field--action {
    margin-top: 15px !important;
  }
}

.fleurdesel-awebooking--standard-1.has-children.has-infants {
  .apb-field--child,
  .apb-field--adult,
  .apb-field--infants {
    .apb-field-group {
      padding-left: 80px;
    }
  }

  .apb-select {
    padding-right: 20px !important;
  }
}

.fleurdesel-single-form-vertical.has-children.has-infants {
  .apb-sidebar_group .apb-field {
    width: 33.33%;
  }
}

.room-detail {
  .fleurdesel-awebooking--standard-1.has-children.has-infants {
    .apb-field-group {
      padding-left: 100px !important;
    }
  }
}

@media (min-width: 992px) {
  .awebooking--modern .fleurdesel-awebooking--standard-1.has-children.has-infants .apb-content > .apb-field {
    width: 25% !important;
  }
}

@media (min-width: 1200px) {
  .awebooking--modern .fleurdesel-awebooking--standard-1.has-children.has-infants .apb-content > .apb-field .apb-input, .awebooking--modern .apb-content > .apb-field .apb-select {
    padding-left: 30px !important;
    padding-right: 5px !important;
  }
}

.apb-check-avb-form {
  input {
    cursor: pointer;
  }

  ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: #333 !important;
    font-size: 16px
  }
  ::-moz-placeholder { /* Firefox 19+ */
    color: #333 !important;
    font-size: 16px
  }
  :-ms-input-placeholder { /* IE 10+ */
    color: #333 !important;
    font-size: 16px
  }
  :-moz-placeholder { /* Firefox 18- */
    color: #333 !important;
    font-size: 16px
  }
}
